
import { defineComponent, reactive, toRefs, computed, onMounted, ref } from 'vue'
import axios from 'redaxios'
import BlogHeader from '@/components/BlogHeader.vue'
import PatchMeta from '@/components/PatchMeta.vue'
import paginate from '@/utils/paginate'
import { PostIndex } from '@/types/PostIndex'
import router from '@/router'

const PAGE_SIZE = 4

export default defineComponent({
  components: {
    PatchMeta,
    BlogHeader
  },
  props: {
    section: String
  },
  async setup (props) {
    const toSection = ref()
    onMounted(() => {
      toSection.value = async (section: string) => {
        await router.push(`/${section}`)
        location.reload()
      }
    })

    const { data } = await axios.get('blog_store/posts_index.json')
    const postsCollection: PostIndex[] = data
    const state = reactive({
      currentPage: 1,
      startPage: 1,
      endPage: 1,
      midPages: [1]
    })

    const activePosts = computed(() => {
      const { startPage, endPage, startIndex, endIndex } = paginate(postsCollection.length, state.currentPage, PAGE_SIZE)
      state.startPage = startPage
      const prev = state.currentPage - 1 >= startPage ? state.currentPage - 1 : 0
      const next = state.currentPage + 1 <= endPage ? state.currentPage + 1 : 0
      state.midPages = [prev, state.currentPage, next].filter(p => p > startPage && p < endPage)
      state.endPage = endPage
      const postsSlice = postsCollection.slice(startIndex, endIndex + 1)
      return props.section ? postsSlice.filter(({ section }) => section === props.section) : postsSlice
    })

    return {
      ...toRefs(state),
      activePosts,
      toSection
    }
  }
})
